import Footer from "../components/Footer";
import ContactForm from "../components/ContactForm";

function Contact() {
  return (
    <>
      <Navbar />
      <Hero
        cName="hero-mid"
        heroImg={AboutImg}
        title="Find The Best Trips In Your Budget "
        text=""
      />
      <ContactForm />
      <Footer />
    </>
  );
}

export default Contact;
