import AboutUs from "./AboutUs";
import "./AboutUsStyles.css";

function AboutUsData(props) {
  return (
    <div className="l-card">
      <div className="l-image">
        <img src={props.image} alt="imag" />
      </div>
      <h4>{props.heading}</h4>
      <h5>{props.ranks}</h5>
      <p>{props.text}</p>
    </div>
  );
}

export default AboutUsData;
