import "./SignUpStyles.css";

function SignUpForm() {
  return (
    <div className="sign">
      <h1>SignUp/Login</h1>
      <form>
        <input placeholder="Enter Name" />
        <input placeholder="Email" />
        <input placeholder="Contact" />
        <input placeholder="password" />
        <button>Submit</button>
      </form>
    </div>
  );
}
export default SignUpForm;
