import Mountain1 from "../assets/2.jpg";
import Mountain2 from "../assets/6.jpg";
import Mountain3 from "../assets/3.jpg";
import Mountain4 from "../assets/10.jpg";
import Mountain5 from "../assets/goa1.jpg";
import Mountain6 from "../assets/goa2.jpg";
import Mountain7 from "../assets/leh1.jpg";
import Mountain8 from "../assets/leh2.jpg";

import DestinationData from "./DestinationData";
import "./DestinationStyles.css";

const Destination = () => {
  return (
    <div className="destination">
      <h1>Trending Destination</h1>
      <p>Tours give you the opportunity to explore nature and find youself.</p>

      <DestinationData
        className="first-des"
        heading="Shimla,Himachal Pradesh"
        text="There’s a reason that Shimla is known
       as the Queen of Hill  Stations, and even
        if you were to take a glance at the most  unremarkable photographs
         of the destination, you will know why.
       This evergreen beauty of Himachal Pradesh draws throngs of tourists
        year after year for its pleasant summers and snowy winters, thanks
         to its hilly terrain scented by pine and signature colonial era
          architecture."
        img1={Mountain1}
        img2={Mountain2}
      />

      <DestinationData
        className="first-des-reverse"
        heading="Jaipur,Rajasthan"
        text="India’s official ‘pink city’, there’s much more to Rajasthan’s capital
       than its pink-red-tinged buildings and historic splendour. This majestic home to erstwhile royals is 
       complete with old-world charm surrounded by colorful markets
        and chaotic streets. 
        Watch fascinatingly carved windows of the Palace of Winds, Hawa Mahal,
         light up with the sun. Right on the main road, all you need to do is
          wake up early for 
        a perfect start to your day with the silhouette of Hawa Mahal against
         the brightening sky.
    "
        img1={Mountain3}
        img2={Mountain4}
      />
      <DestinationData
        className="first-des"
        heading="GOA:Party Capital Of India"
        text="Goa is the perfect mix of natural beauty, lively nightlife, and 
       tropical environs. Check out our Goa blog portal, where you will find all the essential
        information to help you plan an awesome sauce vacation to the party-state. From the best tourist
         attractions to plush resorts, this collection of travel blogs on Goa will make your trip fun and hassle-free. Discover the tropical state's best-kept secrets in these blogs. Each Goa travel blog showcase essential information such as the best time to visit, how to reach, and other nitty-gritty tips and information for you to enjoy your vacation fully. We have treated eve
       ry Goa tour blog as a platform to showcase our love and fervor for India’s favorite party destination."
        img1={Mountain5}
        img2={Mountain6}
      />

      <DestinationData
        className="first-des"
        heading="Leh & Ladakh"
        text="Ladakh is a place like no other.
        The beauty of the place can’t be expressed in words
         Visiting Leh Ladakh can be a great experience as these 
         places have a bounty of natural beauty. There is a bundle 
         of attractions in Leh Ladakh, that are filled with extreme 
         pristine beauty. Ladakh, with a unique characteristic that is 
         a blend of Indian, Tibetan as well as Buddhism linings, is a place
          that is worth paying a visit to. So, scroll down to know what all awaits
        you while you choose from the best places to visit in Leh Ladakh."
        img1={Mountain7}
        img2={Mountain8}
      />
    </div>
  );
};

export default Destination;
