import Serviceinfo from "./Serviceinfo";
import "./TripStyles.css";

function ServiceData(props) {
  return (
    <div className="s-card">
      <div className="s-image">
        <img src={props.image} alt="imag" />
      </div>
      <h4>{props.heading}</h4>
    </div>
  );
}

export default ServiceData;
