import Hero from "../components/Hero";
import Navbar from "../components/Navbar";
import Destination from "../components/Destination";
import Trip from "../components/Trip";
import Footer from "../components/Footer";

function Home() {
  return (
    <>
      <Navbar />
      <Hero
        cName="hero"
        heroImg="https://cdn.pixabay.com/photo/2015/10/30/20/13/boat-1014711_960_720.jpg"
        title="Experience Like Never Before "
        text="Travelling is, indeed, very pleasant. It is useful too. By traveling to new places we can know new people and learn 
      many new things. Travelling opens new vistas of experience and 
      understanding for us"
      />
      <Destination />
      <Trip />
      <Footer />
    </>
  );
}

export default Home;
